$max-width-breakpoint: 1200px;
$mobile-width-breakpoint: 1080px;

$max-view-width: 90vw;

@mixin mobile-only {
  @media (max-width: ($mobile-width-breakpoint - 1)) { @content; }
}
@mixin desktop-only {
  @media (min-width: $mobile-width-breakpoint) { @content; }
}
@mixin small-desktop-only {
  @media screen and (min-width: ($mobile-width-breakpoint)) and (max-width: (1920px - 1)) { @content; }
}

%flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
  overflow-x: hidden;
  overflow-y: hidden;
}

%layout {
  @media screen and (min-width: 1230px) {
    max-width: 1200px;
    margin: 40px auto;
  }
  @include small-desktop-only {
    max-width: 75em;
    margin: 2.5em auto;
  }
  @include mobile-only {
    margin: 36px 15px 24px;
  }
}

%blur-bg {
  background: rgba(255, 255, 255, 0.03);
  backdrop-filter: blur(14px);
}

%hide-mobile {
  @media (max-width: ($mobile-width-breakpoint - 1)) {
    display: none;
  }
}
%hide-desktop {
  @media (min-width: $mobile-width-breakpoint) {
    display: none;
  }
}


.page-head {
  @extend %layout;

  .link {
    background-color: rgba(255, 255, 255, 0.1);
    margin: 1rem auto;
    padding: 0.6rem 1rem;
    text-align: center;
    max-width: 160px;
    border-radius: 5px;
    a {
      color:#ded0f9 ;
      text-decoration: underline;
    }
  }
}

@keyframes animateTitle {
  from {
    background-position: 0%;
  }
  to {
    background-position: 100%;
  }
}

%gradient-title {
  background: linear-gradient(94.96deg, #FFF7F7 0.41%, #B1E3FF 59.54%, #F8AAFF 109.58%);
  font-size: 72px;
  font-weight: 900;
  line-height: 86px;
  align-items: center;
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;

  background-size: 300%;
  animation: animateTitle 2s infinite alternate;
  @include mobile-only {
    font-size: 42px;
    text-align: center;
  }
}

%dim-content {
  font-size: 18px;
  line-height: 18px;
  margin-top: 18px;
  color: rgba(255, 255, 255, 0.6);
  text-align: center;
}

.gradient-title {
  @extend %gradient-title;
  text-align: center;
  color: #fff;
  font-size: 40px;
  line-height: 40px;

  @media (max-width: ($mobile-width-breakpoint - 1)) {
    font-size: 32px;
  }

  &.uppercase{
    text-transform: uppercase
  }
}

.dim-content {
  @extend %dim-content;

  @media (max-width: ($mobile-width-breakpoint - 1)) {
  font-size: 14px;
  line-height: 22px;
  margin-top: 4px;
  }
}

.window-for-tabs-pane {
  background: #fff;
  position: relative;
  cursor: default;
  margin: 26px auto;
  color: #333;
  padding: 30px 24px;
  max-width: min(728px, 90vw);
  background: #FFFFFF;
  border-radius: 0px 0px 15px 15px;
  @include mobile-only {
    width: calc(100% - 30px);
    padding: 30px 15px;
  }
}

%btn {
  border-radius: 10px;
  width: clamp(100px, 210px, 50%);
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  line-height: 24px;
  color: #FFFFFF;
  cursor: pointer;
  &:hover {
    // background-size: 300%;
    // animation: animateTitle 2s infinite alternate;
  }
 }

.purple-btn {
  @extend %btn;
  background: linear-gradient(79.72deg, #4719FD 4.86%, #E64DFF 103.07%);
}

.dark-btn{
  @extend %btn;
  background: rgba(233, 223, 255, 0.2);
}

.cur-input {
  margin-top: 7px;

  input {
    border: 1px solid rgba(151, 151, 151, 0.3) !important;

    &:hover,
    &:focus {
      border: 1px solid #5915E1 !important;
    }
  }
}