.window {
  h1 {
    text-align: center;
  }
}

.nowrap,
.title1 {
  white-space: nowrap;
}

#root .swap {
  align-items: flex-end;

  select {
    width: 130px;
  }

  input {
    width: 230px;
  }

  img {
    margin: 13px 10px;
    width: 20px;
    // -webkit-filter: invert(20%) sepia(12%) saturate(9%) hue-rotate(159deg) brightness(93%) contrast(88%);
    // filter: invert(20%) sepia(12%) saturate(9%) hue-rotate(159deg) brightness(93%) contrast(88%);
  }
}

.swaprates-icon {
  width: 13px;
  margin: 0 5px;
  cursor: pointer;
  // opacity: 0;
  // width: 0;
}

.swap-block {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;

  >div {
    &:first-child {
      margin-right: 4px;
    }
  }
}

.ant-select-item-option-selected:not(.ant-select-item-option-disabled) {
  color: #7c8ead !important;
}

.ant-select-dropdown {
  background-color: #f00;

  .ant-select-item-option-content {
    display: flex;
    align-items: center;

    >img {
      width: 16px;
      margin: 0;
      margin-right: 5px;
    }
  }
}

#root .table-swap .ant-table {
  margin-top: 16px;

  .name {
    text-align: left;
    color: #999999;
    // white-space: nowrap;
    // overflow: hidden;
    // text-overflow: ellipsis;
    // display: inline-block;
    // width: 100%;
  }

  .column {
    // align-items: flex-start !important;
  }

  tr {
    display: flex;
    align-items: center;
  }

  .ant-table-thead>tr>th {
    padding: 10px 5px;
  }

  .ant-table-thead>tr>th,
  .ant-table-tbody>tr>td {
    width: 130px;
    text-align: left;
    font-size: 13px;
    padding-top: 10px;

    // &:nth-child(1) {
    //   width: 66px;
    // }

    &:nth-child(1) {
      width: 178px;
    }

    &:nth-child(3) {
      width: 100px;
      white-space: nowrap;
    }

    &:last-child {
      width: 90px;
      text-align: right;
    }

    .long {
      display: flex;
      align-items: center;
    }

    .long img {
      width: 24px;
      height: 24px;
    }

    .long img:not(:first-child) {
      margin-left: -4px;
    }

    .long img:first-child {
      margin-left: -2px;
    }

    .swap-pool-img {
      min-width: 50px;
    }

    .long .start {
      align-items: flex-start;
      // margin-left: 9px;
    }
  }

  .ant-table-tbody>tr:nth-child(2),
  .ant-table-tbody>tr:nth-child(3) {
    cursor: pointer;
  }
}

.mobile-top {
  cursor: pointer;
}

.mobile-rotate {
  transform: rotate(90deg);
}

.mobile-rotate-new {
  transform: rotate(0deg);
}

.apys {
  position: relative;
  align-items: flex-start !important;

  >div {
    position: relative;
    width: 49%;
    // height: 206px;
    padding: 30px 24px;
    border-radius: 15px;
    box-shadow: 0px 20px 40px rgb(148 148 148 / 6%);
    background: #fff;

    &:first-child {
      margin-right: 20px;
    }

    .btns {
      position: absolute;
      bottom: 36px;
      left: 50%;
      transform: translateX(-50%);
    }
  }
}

@media screen and (max-width: 800px) {
  #root .flex.swap {
    flex-direction: column;
    align-items: flex-start;

    .mobile-top {
      margin-top: 10px;
    }

    .mobile-rotate {
      transform: rotate(0deg);
      // -webkit-transform: rotate(90deg);
      margin: 15px auto 5px;
    }

    .mobile-rotate-new {
      transform: rotate(90deg);
    }
  }

  .mobile-text {
    font-size: 12px;
  }

  .swap-block {
    max-width: 400px;
    width: 100%;

    .ant-select {
      width: 40% !important;
    }

    >div:first-child {
      margin-right: 0;
    }

    >input,
    >div:nth-child(2) {
      width: 59% !important;

      >input {
        width: 100% !important;
      }
    }
  }

  #root .table-swap .ant-table {

    .ant-table-thead>tr>th,
    .ant-table-tbody>tr>td {
      width: 100px;
      text-align: left;

      &:nth-child(2) {
        width: 120px;
      }

      &:last-child {
        width: 60px;
      }
    }
  }

  #root .flex.apys {
    display: block !important;

    >div {
      width: 100%;
      padding: 30px 15px;

      &:first-child {
        margin-bottom: 2em;
      }
    }

    fieldset {
      display: block;
    }

    fieldset:nth-child(2) {
      margin-top: 30px;
    }

    fieldset:nth-child(2) .btns a {
      display: block;
    }

    fieldset:nth-child(2) .btns a:nth-child(2) {
      margin-top: 5px;
    }
  }
}

@media screen and (max-width: 500px) {
  .title1 {
    white-space: normal;
  }

  #root .window.pl-0 .ant-table table tr {
    padding: 0 10px;
  }

  #root .table-swap .ant-table {
    table {
      width: 450px !important;
    }

    .ant-table-thead>tr,
    .ant-table-tbody>tr {
      justify-content: flex-start;
    }

    .ant-table-thead>tr>th,
    .ant-table-tbody>tr>td {
      width: 110px;
      text-align: left;
      font-size: 12px;

      &:nth-child(1) {
        width: 130px;
      }
    }

    .ant-table-tbody>tr>td .long {
      .start {
        margin-left: 0;
      }

      .swap-pool-img {
        min-width: auto !important;
      }

      img {
        display: none;
      }
    }
  }
}


@import './var.scss';

.stable-swap {
  @extend %layout;
  text-align: center;
  .title {
    @extend %gradient-title;
  }
  .content {
    color: #fff;
  }
  .box {
    background: #FFFFFF;
    border-radius: 15px;
    margin: 2rem;
    padding: 2rem;
  }
}